@import url(https://fonts.googleapis.com/css?family=Open+Sans|Roboto&display=swap);
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1rem;
    line-height: 1.5;
    color: #333;
    overflow-x: hidden;
}

.v-header{
    height: 100vh;
    display: flex;
    align-items: center;
    color: #fff;
}

.container{
    max-width: 960px;
    padding-left: 1rem;
    padding-right: 1rem;
    margin: auto;
    text-align: center;
}

.fullscreen-video-wrap{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.fullscreen-video-wrap > video{
    min-width: 100%;
    min-height: 100%;
    -webkit-transform: translate(-1%, 0);
            transform: translate(-1%, 0);
}

.header-overlay{
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
    left: 0;
    background: #3d5a80;
    z-index: 1;
    opacity: 0.50;
}

.header-content{
    z-index: 2;
    text-align: center;
}

.title-image{
    font-size: 50px;
    margin-bottom: 0;
    margin-top: 0;
    /*width: 80%;*/
}

.header-content > p{
    font-size: 1rem;
    display: block;
}

.btn {
    background: #ee6c4d;
    color: #fff;
    font-size: 1.2rem;
    padding: 1rem 2rem;
    text-decoration: none;
}

.btn:hover {
    background: #eb5a36;
    cursor: pointer;
}

.btn:focus {outline:0;}

.center{
    text-align: center;
    margin: auto;
}

.alert {
    margin: 5% 0;
    width: 100%;
    height: 2.5rem;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    display: none;
}

.alert > div {
    margin: auto 0;
    height: 55%;
    width: 55%;
    float: left;
    display: flex;
    align-items: center;
}

.alert > div > p {
    margin: auto;
}

.alert-img{
    height: 100%;
    margin: auto 0;
    width: auto;
    float: left;
}


@media(max-width:640px){
    .container{
        padding-right: 3rem;
        padding-left: 3rem;
    }
    .fullscreen-video-wrap > video{
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
    }
    .header-content > p {
        font-size: 1rem;
    }
    /*.title-image{
        width: 40%;
    }*/

    .alert {
        height: 5rem;
    }
    .alert-img {
        height: 45%;
    }
    .alert > div {
        width: 65%;
    }
}

@media(max-width:1310px){
    .fullscreen-video-wrap > video{
        -webkit-transform: translate(-30%, 0);
                transform: translate(-30%, 0);
    }
    /*.title-image{
        width: 60%;
    }*/
}
.mailing {
    width: 75%;
    height: 2.5rem;
    margin: auto;
    overflow: auto;
}

.mailing > input {
    width: 80%;
    height: 100%;
    box-sizing: border-box;
    border: none;
    float: left;
    padding: 10px;
    vertical-align: middle;
}

.mailing > input:focus {
    outline: 0;
    align-content: center;
}

::-webkit-input-placeholder {
   text-align: center;
}

:-moz-placeholder { /* Firefox 18- */
   text-align: center;  
}

::-moz-placeholder {  /* Firefox 19+ */
   text-align: center;  
}

:-ms-input-placeholder {  
   text-align: center; 
}

.mailing > .btn{
    width: 20%;
    height: 100%;
    box-sizing: border-box;
    border: none;
    float: left;
    padding: 0;
    text-align: center;
}

.mailing > .btn > strong{
    font-size: 1rem;
    margin: 0;
}

@media(max-width:640px){
    .mailing{
        height: 5rem;
        overflow: hidden;
    }
    .mailing > input {
        width: 100%;
        height: 45%;
        float: clear;
        margin-bottom: 4%;
    }
    .mailing > .btn {
        margin: auto;
        width: 40%;
        height: 45%;
        float: none;
    }
}
.SUSU{
    position: absolute;
    top: 90%;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    margin: 0;
    width: 100%;
    height: 2.5rem;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
}

.SUSU > div {
    margin: auto 0;
    width: 60%;
    height: 55%;
    float: left;
    display: flex;
    align-items: center;
}

.SUSU > div > p {
    margin-right: 12px;
    margin-left: auto;
}

.SUSU > div > a{
    height: auto;
    margin: auto 0;
    margin-right: auto;
    width: 40%;
    max-width: 96px;
    float: left;
}

.SUSU > div > a:visited{
    text-decoration: none;
}

.SUSU > div > a:hover{
    cursor: pointer;
}

@media(max-width:640px){
    .SUSU > div {
        width: 80%;
    }
    .SUSU > div > p  {
        margin-right: 6px;
    }
}

/*
font-family: 'Roboto', sans-serif;
font-family: 'Open Sans', sans-serif;
*/


.committee > h2, h3, h4 {
    font-family: 'Roboto', sans-serif;
}

.committee > h2 {
    font-size: 2rem;
    letter-spacing: 0.20rem;
}

.member-content > h3 {
    font-size: 1.5rem;
}
.member-content > h4 {
    font-size: 1.10rem;
    color: grey;
}

.member-content > p {
    font-family: 'Open Sans', sans-serif;
}


.slick-track {
    overflow: hidden;
}

.committee {
    text-align: center;
    padding: 3rem 0  2rem 0;
    overflow: hidden;
    background-image: url('/img/plane.svg?raw=true');
    background-repeat: repeat-x;
}


.member {
    width: 20%;
    padding: 0.5rem;
    text-align: center;
    min-height: 540px;


}
.member-container {
    float: left;
    text-align: center;
    border-radius: 8px;
    min-height: 540px;
    height: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.member:focus{
    outline: none;
}

.member-container> img {
    width: 70%;
    border: solid 0 black;
    margin: 1rem auto;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.member-content{
    color: 293241;
    padding: 1rem;
}

.member-content > h3{
    margin: 0;
    color: #ee6c4d;
}

.member-content > h4 {
    margin: 0;
}

.member-content > p{
    margin: 0;
    margin-top: 0.5rem;
    
}
